@keyframes pop {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(0.8);
        
    }

    100% {
        transform: scale(1);
    }
}

@keyframes navtogglein {
    from {
        opacity:0;
        transform: translate(0,-2rem)
    }

    to {
        opacity:1;
        transform: translate(0,0);
    }
}


@keyframes navitemin {
    from {
       
       
    }

    to {
        opacity:1;
        transform: scale(1) translate(0,0);
    }
}

@keyframes jump {
    0% {
        transform: translate(0 ,0 )
    }
    50% {
        transform: translate(0 ,-0.3rem )
    }
    100% {
        transform: translate(0 ,0 );
    }

}

%position {
    position:absolute;
    top:0;left:0;right:0;bottom:0;
}

%pseudo {
    display:block;
    content:'';
}

