      animation-delay: 0.6s; }
    #nav-main-toggle:checked + .site-header .navigation li:nth-child(5) {
      -webkit-animation-delay: 0.75s;
      animation-delay: 0.75s; }
    #nav-main-toggle:checked + .site-header .navigation li:nth-child(6) {
      -webkit-animation-delay: 0.9s;
